
import More from '@/components/more';
import { iconNew, iconLi } from '@/assets/img';
import styles from './index.module.less';

const HomeActivityCenter = () => {
    const list = [
        {
            title: '',
            isNew: true,
            desc: ''
        },
        {
            title: '',
            isNew: false,
        },
        {
            title: '',
            isNew: false,
        },
        {
            title: '',
            isNew: false,
        },
    ];
    return (
        <div className={styles.HomeActivityCenter}>
            <div className={styles.top}>
                <span></span>
                <More />
            </div>
            <div className={styles.block}>
                <div className={styles.l}>
                    <img className={styles.cover} src="" alt="" />
                    <span className={styles.title}></span>
                </div>
                <div className={styles.r}>
                    {
                        list.map((item, index) => (
                            <div className={styles.item} key={index}>
                                <div className={styles.titleWrap} style={{alignItems: item.isNew ? 'flex-start' : 'center'}}>
                                    <img className={styles.icon} src={item.isNew ? iconNew : iconLi} alt="" />
                                    <span className={styles.title}>{item.title}</span>
                                </div>
                                {
                                    item.isNew && <span className={styles.desc}>{item.desc}</span>
                                }
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        </div>
    );
}

export default HomeActivityCenter;