
// import More from '@/components/more';
// import { iconPrice } from '@/assets/img';
import Level from '@/components/level';
import InfoList, { InfoListProps } from '@/components/infoList';
import Paragraph from 'antd/es/typography/Paragraph';
import styles from './index.module.less';


const UserCard = () => {
    const list: InfoListProps['list'] = [
        {
            label: '',
            value: '20',
            routePath: '/user-center?us_tab=6'
        },
        {
            label: '',
            value: '20',
            routePath: '/user-center?us_tab=7'
        },
        {
            label: '',
            value: '20',
            routePath: '/user-center?us_tab=7'
        },
    ];

    return (
        <div className={styles.homeUserCard}>
            <div className={styles.top}>
                <img className={styles.avatar} src="" alt="" />
                <div>
                    <p className={styles.userName}></p>
                    <Level />
                </div>
            </div>
            <Paragraph ellipsis={{ rows: 2, tooltip: true }} className={styles.desc}>F10Net</Paragraph>
            <InfoList list={list} />
        </div>
    );
};

export default UserCard;