
import Paragraph from 'antd/es/typography/Paragraph';
import { iconVideo, iconEye } from '@/assets/img';
import styles from './index.module.less';

// 
const VideoList = () => {
    return (
        <div className={styles.videoList}>
            {
                [1,2,3,4].map((_item, index) => (
                    <div key={index} className={styles.item}>
                        <div className={styles.coverWrap}>
                            <img className={styles.cover} src="" alt="" />
                            <div className={styles.mask}>
                                <img className={styles.icon} src={iconVideo} alt="" />
                            </div>
                        </div>
                        <Paragraph ellipsis={{ rows: 2, tooltip: true}} className={styles.title}></Paragraph>
                        <div className={styles.btm}>
                            <span>2022-05-03  15:56</span>
                            <div className={styles.r}>
                                <span>1268</span>
                                <img src={iconEye} alt="" />
                            </div>
                        </div>
                    </div>
                ))
            }
            
            
        </div>
    )
}

export default VideoList;