

import { iconSearch, iconPen } from '@/assets/img';
import styles from './index.module.less';


const PostSearchBlock = () => {
    const historySearch = ['', 'SFT000', '', '', 'web'];

    return (
        <div className={styles.block}>
            <div className={styles.searchWrap}>
                <input type="text" placeholder='' />
                <img src={iconSearch} alt="" />
            </div>
            <div className={styles.historyWrap}>
                <span className={styles.h3}></span>
                <ul>
                    {
                        historySearch.map((item, index) => <li key={index}>{item}</li>)
                    } 
                </ul>
            </div>
            <div className={styles.btn}>
                <img src={iconPen} alt="" />
                <span></span>
            </div>
        </div>
    );
}

export default PostSearchBlock;