
import { iconPrice } from '@/assets/img';
import { IntegralType } from '@/interface';
import { IntegralTypeIconMap } from '@/constant';
import styles from './index.module.less';

export interface TaskItemProps {
    type: IntegralType;
    title: string;
    desc: string;
    count: number;
    isDone: boolean;
    hideBtn?: boolean;
}

const Task = () => {
    // 
    const taskList: TaskItemProps[] = [
        {
            type: IntegralType.PUTONG,
            title: "",
            desc: "2",
            count: 2,
            isDone: false,
        },
        {
            type: IntegralType.QIUZHU,
            title: "",
            desc: "2",
            count: 2,
            isDone: false,
        },
        {
            type: IntegralType.FENXIANG,
            title: "",
            desc: "2",
            count: 2,
            isDone: false,
        },
        {
            type: IntegralType.GUSHI,
            title: "",
            desc: "2",
            count: 2,
            isDone: false,
        },
        {
            type: IntegralType.PINGLUN,
            title: "",
            desc: "2",
            count: 2,
            isDone: false,
        },
        {
            type: IntegralType.JINGHUA,
            title: "",
            desc: "2",
            count: 2,
            isDone: false,
            hideBtn: true,
        },
        {
            type: IntegralType.ZHUCE,
            title: "",
            desc: "2",
            count: 2,
            isDone: true,
        },
        {
            type: IntegralType.ZILIAO,
            title: "",
            desc: "2",
            count: 2,
            isDone: true,
        },
        {
            type: IntegralType.DENGLU,
            title: "",
            desc: "2",
            count: 2,
            isDone: true,
        },
    ];
    
    return (
        <div className={styles.listWrap}>
            {
                taskList.map(({ type, title, desc, count, isDone, hideBtn }, index) => (
                    <div className={styles.taskItem} key={index}>
                        <img className={styles.icon} src={IntegralTypeIconMap[type]} alt="" />
                        <p className={styles.title}>{title}</p>
                        <p className={styles.desc}>{desc}</p>
                        <div className={styles.count}>
                            <img src={iconPrice} alt="" />
                            <span>+{count}</span>
                        </div>
                        {
                            !hideBtn && (
                                isDone ? <div className={styles.btnDone}></div> : <div className={styles.btnDo}></div>
                            )
                        }
                    </div>
                ))
            }
            
        </div>
    )

}

export default Task;