import { useState } from 'react';

// import { useHistory } from 'react-router-dom';
import BreadCrumb, { BreadCrumbProps } from '@/components/breadCrumb';
import Paragraph from 'antd/es/typography/Paragraph';
import MyPagination from '@/components/pagination';
import styles from './index.module.less';

const NoticeList = () => {
    // const history = useHistory();
    const crumb: BreadCrumbProps['list'] = [
        {
            name: '',
            route: '/home',
        },
        {
            name: ''
        }
    ];
    const defaultPager = {
        current: 1,
        pageSize: 10,
        total: 100
    };
    const [pager, setPager] = useState(defaultPager);

    return (
        <div className={styles.noticeList}>
            <BreadCrumb list={crumb} />
            <div className={styles.main}>
                <ul className={styles.list}>
                    {
                        [1,2,3,4,5].map((_item, index) => (
                            <li key={index}>
                                <div className={styles.l}>
                                    <span className={styles.day}>11</span>
                                    <div>
                                        <span className={styles.month}>02</span>
                                        <span className={styles.year}>2024</span>
                                    </div>
                                </div>
                                <div className={styles.r}>
                                    <div className={styles.t}>
                                        <p className={styles.title}>InPlantSCADA()</p>
                                        <Paragraph ellipsis={{ rows: 2 }} className={styles.desc}>SCADASCADA</Paragraph>
                                    </div>
                                    <div className={styles.btn}></div>
                                </div>
                            </li>
                        ))
                    }
                    
                </ul>
            </div>
            <MyPagination current={pager.current} onChange={(v) => setPager(o => ({ ...o, current: v}))} total={pager.total} />
        </div>
    );
}

export default NoticeList;